import React, { useEffect, useState } from "react";
import { Box, MotionDiv } from "../styles";
import { Btn, WhiteBtn } from "../components/Btn";
const scrollTop = () => {
  return Math.max(
    window.pageYOffset,
    document.documentElement.scrollTop,
    document.body.scrollTop
  );
};

export default function ScrollBar(props) {
  const [isTop, setIsTop] = useState(true);
  //   console.log(scrollYProgress);

  const onScroll = () => {
    const position = scrollTop();
    if (position >= 80) {
      setIsTop(false);
    } else {
      setIsTop(true);
    }
  };

  useEffect(() => {
    document.addEventListener("scroll", onScroll);
    return () => document.removeEventListener("scroll", onScroll);
  });

  return (
    <Box
      display={["block", "none"]}
      position="fixed"
      zIndex={2}
      bottom={0}
      left={0}
      width="100%"
    >
      <MotionDiv
        bg="white"
        className="flex items-center justify-center"
        flexDirection="row"
        initial={{
          height: 0,
          marginBottom: 0,
          display: "none",
          opacity: 0,
          y: 0,
        }}
        variants={{
          active: {
            opacity: 1,
            display: "flex",
            height: "auto",
            y: 40,
            marginBottom: 40,
          },
          inactive: {
            marginBottom: 0,
            opacity: 0,
            y: 0,
            height: "0",
            transitionEnd: {
              display: "none",
            },
          },
        }}
        animate={!isTop ? "active" : "inactive"}
        transition={{ duration: 0.2 }}
      >
        <Btn
          fontSize={14}
          px={0}
          width={["100%", "inherit"]}
          to="./reservation"
          mb={[0, 0]}
          mr={[0, 20]}
        >
          撮影を予約する
        </Btn>
        <WhiteBtn
          px={0}
          fontSize={14}
          width={["100%", "inherit"]}
          to={"/contact"}
        >
          お問い合わせ
        </WhiteBtn>
      </MotionDiv>
    </Box>
  );
}
