export function getTypeFee(type) {
  let sum = 0;
  type.map((item) => {
    sum += valueDict[item];
  });
  return sum;
}

export function format(num) {
  let snum = String(num);
  while (snum != (snum = snum.replace(/^(-?\d+)(\d{3})/, "$1,$2")));
  return "¥" + snum;
}

